<template>
  <div class="main">
    <v-card>
      <v-card-title primary-title class="headline secondary">
        <span style="color: #ffffff">{{ question.question }}</span>
      </v-card-title>
      <v-card-text>
        <v-list>
          <v-list-tile v-for="answer in question.answers" :key="answer.id">
            <v-list-tile-action>
              <v-btn
                icon
                ripple
                @click="answerQuestion(answer)"
              >
                <v-icon v-if="!answer.checked">check_box_outline_blank</v-icon>
                <v-icon v-else>check_box</v-icon>
              </v-btn>
            </v-list-tile-action>
            <v-list-tile-content>{{ answer.answer }}</v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-card-text>
      <v-card-actions>
        <v-btn
          color="primary"
          @click="previousQuestion"
        ><v-icon>navigate_before</v-icon>&nbsp;Previous</v-btn>
        <v-btn
          color="primary"
          @click="nextQuestion"
        ><v-icon>navigate_next</v-icon>&nbsp;Next</v-btn>
        <v-btn
          color="error"
          @click="finishGame"
        ><v-icon>stop</v-icon>&nbsp;Finish</v-btn>
      </v-card-actions>
    </v-card>
    <v-snackbar
      v-model="hasErrors"
      :timeout="5000"
      color="error"
    >
      {{ errors }}
      <v-btn
        dark
        flat
        @click="hasErrors = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import axios from '@/axios'
export default {
  name: 'Game',
  data() {
    return {
      wsUrl: process.env.VUE_APP_WS,
      gameID: this.$route.params.gameID,
      question: {
        answers: [],
        question: null
      },
      errors: null,
      hasErrors: false,
      gameWS: null,
      checkConnectionsInterval: null,
      team: {}
    }
  },
  beforeDestroy() {
    clearInterval(this.checkConnectionsInterval)
  },
  created() {
    this.getHomeTeam()
    this.getCurrentQuestion()
    this.connectGameWS()
    let vue = this
    vue.checkConnectionsInterval = setInterval(() => {
      vue.checkConnections()
    }, 5000)
  },
  methods: {
    getHomeTeam() {
      axios
        .get('/api/v1/games/' + this.gameID + '/home')
        .then(resp => {
          console.log('Got home team: ', resp.data)
          this.team = resp.data
          if (this.question && this.team.answers) {
            this.team.answers.map(answer => {
              let found = this.question.answers.find(a => a.id === answer.id)
              if (found) {
                found.checked = true
              }
            })
          }
        })
        .catch(err => {
          console.error('Could not get home team: ', err.response)
          this.errors = 'Could not get home team: ' + err.response.data
          this.hasErrors = true
        })
    },
    getCurrentQuestion() {
      axios
        .get('/api/v1/games/' + this.gameID + '/current')
        .then(resp => {
          this.question = resp.data
          if (this.team && this.team.answers) {
            this.team.answers.map(answer => {
              let found = this.question.answers.find(a => a.id === answer.id)
              if (found) {
                found.checked = true
              }
            })
          }
        })
        .catch(err => {
          console.error('Could not get current question: ', err.response)
          this.errors = 'Could not get current question: ' + err.response.data
          this.hasErrors = true
        })
    },
    nextQuestion() {
      axios
        .post('/api/v1/games/' + this.gameID + '/next')
        .then(() => {
          this.gameWS.send(this.gameID)
        })
        .catch(err => {
          console.error('Could not get next question: ', err.response)
          this.errors = 'Could not get next question: ' + err.response.data
          this.hasErrors = true
        })
    },
    previousQuestion() {
      axios
        .post('/api/v1/games/' + this.gameID + '/previous')
        .then(() => {
          this.gameWS.send(this.gameID)
        })
        .catch(err => {
          console.error('Could not get previous question: ', err.response)
          this.errors = 'Could not get previous question: ' + err.response.data
          this.hasErrors = true
        })
    },
    finishGame() {
      axios
        .post('/api/v1/games/' + this.gameID + '/finish')
        .then(() => {
          this.gameWS.send(this.gameID)
          this.$router.replace({
            name: 'finished',
            params: { gameID: this.gameID }
          })
        })
        .catch(err => {
          console.error('Could not finish game: ', err.response)
          this.errors = 'Could not finish game: ' + err.response.data
          this.hasErrors = true
        })
    },
    connectGameWS() {
      this.gameWS = new WebSocket(this.wsUrl + '/ws/pbe/game/' + this.gameID)
      this.gameWS.onopen = () => {
        console.log('Connected to game websocket')
        this.gameWS.send(this.gameID)
      }

      this.gameWS.onmessage = e => {
        this.question = JSON.parse(e.data)
        if (this.question.finished) {
          this.$router.replace({
            name: 'finished',
            params: { gameID: this.gameID }
          })
        } else {
          this.getHomeTeam()
        }
      }
    },
    checkConnections() {
      console.log('Checking connections')
      if (this.gameWS.readyState === this.gameWS.CLOSED) {
        this.connectGameWS()
      }
    },
    answerQuestion(answer) {
      if (!answer.checked) {
        answer.checked = true
        axios
          .post(
            '/api/v1/games/' +
              this.gameID +
              '/teams/' +
              this.team.id +
              '/answers/' +
              answer.id
          )
          .catch(err => {
            console.error('Could not submit answer: ', answer.id, err.response)
            this.errors = 'Could not submit answer: ' + err.response.data
            this.hasErrors = true
          })
      } else {
        answer.checked = false
        axios
          .delete(
            '/api/v1/games/' +
              this.gameID +
              '/teams/' +
              this.team.id +
              '/answers/' +
              answer.id
          )
          .catch(err => {
            console.error('Could not delete answer: ', answer.id, err.response)
            this.errors = 'Could not delete answer: ' + err.response.data
            this.hasErrors = true
          })
      }
    }
  }
}
</script>
