import axios from 'axios'
import NProgress from 'nprogress'

const instance = axios.create({})

instance.interceptors.request.use(
  request => {
    NProgress.start()
    return request
  },
  error => {
    NProgress.done()
    return Promise.reject(error)
  }
)

instance.interceptors.response.use(
  response => {
    NProgress.done()
    return response
  },
  error => {
    NProgress.done()
    return Promise.reject(error)
  }
)

export default instance
