<template>
  <div class="main">
    <h1 class="display-2">Games</h1>
    <v-btn
      large
      color="primary"
      @click="addingGame = true"
    ><v-icon>add</v-icon>&nbsp;New Game</v-btn>
    <div class="games">
      <v-card v-for="game in games" :key="game.id">
        <v-card-title primary-title class="headline secondary">
          <span style="color: #ffffff">{{ game.name }}</span>
        </v-card-title>
        <v-card-text>
          <v-list>
            <v-list-tile>
              <v-list-tile-content>
                <v-list-tile-title>{{ game.seconds }}</v-list-tile-title>
                <v-list-tile-sub-title>Seconds</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile>
              <v-list-tile-content>
                <v-list-tile-title>{{ game.created | date }}</v-list-tile-title>
                <v-list-tile-sub-title>Created</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile>
              <v-list-tile-content>
                <v-list-tile-title>{{ game.questions }}</v-list-tile-title>
                <v-list-tile-sub-title>Questions</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
          <p class="title">Teams</p>
          <v-list class="teams-div">
            <v-list-tile v-for="team in game.teams" :key="team.id">
              <v-list-tile-title>{{ team.name }}</v-list-tile-title>
              <v-list-tile-action class="team-join-action">
                <v-btn
                  flat
                  icon
                  color="secondary"
                  @click="openTeam(game, team)"
                ><v-icon>launch</v-icon></v-btn>
              </v-list-tile-action>
            </v-list-tile>
          </v-list>
        </v-card-text>
        <v-card-actions>
          <v-btn
            color="primary"
            large
            block
            @click="addTeam(game)"
          ><v-icon>launch</v-icon>&nbsp;Join</v-btn>
          <v-btn
            v-if="game.status === 'OPEN'"
            color="success"
            large
            block
            @click="startGame(game)"
          ><v-icon>play_circle_outline</v-icon>&nbsp;Start</v-btn>
          <v-btn
            v-if="game.status === 'STARTED'"
            color="error"
            large
            block
            @click="finishGame(game)"
          ><v-icon>stop</v-icon>&nbsp;Finish</v-btn>
        </v-card-actions>
        <v-card-actions>
          <v-btn
            color="error"
            large
            block
            @click="deleteGame(game)"
          ><v-icon>delete</v-icon>&nbsp;Delete</v-btn>
        </v-card-actions>
      </v-card>
    </div>

    <v-dialog
      v-model="addingGame"
      width="400"
    >
      <v-card>
        <v-card-title
          class="headline primary"
          primary-title
        >
          <span style="color: #ffffff">Add Game</span>
        </v-card-title>

        <v-card-text>
          <v-text-field
            v-model="game.name"
            label="Game Name"
            required
          />
          <v-text-field
            v-model="game.seconds"
            label="Question Seconds"
            required
            type="number"
            min="1"
          />
          <v-text-field
            v-model="game.questions"
            label="Number of Questions"
            required
            type="number"
            min="1"
          />
          <v-text-field
            v-model="book"
            label="Game Book"
            required
          />
          <v-text-field
            v-model="chapters"
            label="Game Chapters"
            required
            hint="Chapters separated by commas (E.g. 2,3,4,5,21)"
            persistent-hint
          />
        </v-card-text>

        <v-divider/>

        <v-card-actions>
          <v-spacer/>
          <v-btn
            :disabled="cannotAddGame"
            color="secondary"
            @click="addGame"
          >
            <v-icon>add</v-icon> Add
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="addingTeam"
      width="400"
    >
      <v-card>
        <v-card-title
          class="headline primary"
          primary-title
        >
          <span style="color: #ffffff">Join Game</span>
        </v-card-title>

        <v-card-text>
          <v-text-field
            v-model="team.name"
            label="Team Name"
            required
          />
        </v-card-text>

        <v-divider/>

        <v-card-actions>
          <v-spacer/>
          <v-btn
            :disabled="joining"
            color="secondary"
            @click="join"
          >
            <v-icon>add</v-icon> Add
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-snackbar
      v-model="hasErrors"
      :timeout="5000"
      color="error"
    >
      {{ errors }}
      <v-btn
        dark
        flat
        @click="hasErrors = false"
      >
        Close
      </v-btn>
    </v-snackbar>

  </div>
</template>

<script>
import axios from '@/axios'
import format from 'date-fns/format'
export default {
  name: 'Home',
  filters: {
    date(val) {
      return format(val, 'dddd, MMM Do, YYYY h:mm:ss a')
    }
  },
  data() {
    return {
      wsUrl: process.env.VUE_APP_WS,
      games: [],
      gettingGames: false,
      errors: null,
      hasErrors: false,
      addingGame: false,
      game: {
        name: null,
        seconds: 30,
        questions: 10
      },
      creatingGame: false,
      joining: false,
      addingTeam: false,
      team: {
        name: null
      },
      book: null,
      chapters: null,
      teamsWS: null,
      checkConnectionsInterval: null
    }
  },
  computed: {
    cannotAddGame() {
      if (!this.game.name) {
        return true
      }
      if (this.creatingGame) {
        return true
      }
      return false
    }
  },
  beforeDestroy() {
    clearInterval(this.checkConnectionsInterval)
  },
  created() {
    this.getGames()
    this.connectTeamsWS()
    let vue = this
    vue.checkConnectionsInterval = setInterval(() => {
      vue.checkConnections()
    }, 5000)
  },
  methods: {
    getGames() {
      this.gettingGames = true
      axios
        .get('/api/v1/games')
        .then(resp => {
          this.games = resp.data
        })
        .catch(err => {
          console.error('Could not get games: ', err)
          this.errors = 'Could not get games: ' + err.response.data
          this.hasErrors = true
        })
        .finally(() => {
          this.gettingGames = false
        })
    },
    addGame() {
      this.creatingGame = true
      this.game.chapters = []
      let chapters = this.chapters.split(',')
      chapters.map(chapter => {
        this.game.chapters.push({
          book: this.book,
          chapter: chapter
        })
      })
      axios
        .post('/api/v1/games', this.game)
        .then(() => {
          this.getGames()
          this.addingGame = false
        })
        .catch(err => {
          console.error('Could not add game: ', err.response)
          this.errors = 'Could not add game: ' + err.response.data
          this.hasErrors = true
        })
        .finally(() => {
          this.creatingGame = false
        })
    },
    addTeam(game) {
      this.game = game
      this.team = {
        name: null
      }
      this.addingTeam = true
    },
    join() {
      this.joining = true
      axios
        .post('/api/v1/games/' + this.game.id + '/teams', this.team)
        .then(() => {
          this.getGame(this.game.id)
          this.addingTeam = false
        })
        .catch(err => {
          console.error('Could not join game: ' + this.game.name, err.response)
          this.errors =
            'Could not join game: ' + this.game.name + ' : ' + err.response.data
          this.hasErrors = true
        })
        .finally(() => {
          this.joining = false
        })
    },
    getGame(gameID) {
      axios
        .get('/api/v1/games/' + gameID)
        .then(resp => {
          this.game = resp.data
          this.games.forEach((game, pos) => {
            if (game.id === gameID) {
              this.games[pos] = this.game
            }
          })
        })
        .catch(err => {
          console.error('Could not get game: ' + gameID, err.response)
          this.errors =
            'Could not get game: ' + gameID + ' : ' + err.response.data
          this.hasErrors = true
        })
    },
    startGame(game) {
      console.log('starting game: ', game)
      axios
        .post('/api/v1/games/' + game.id + '/start')
        .then(() => {
          this.$router.push({ name: 'game', params: { gameID: game.id } })
        })
        .catch(err => {
          console.error('Could not get game started: ', game, err.response)
          this.errors = 'Could not get started: ' + err.response.data
          this.hasErrors = true
        })
    },
    finishGame(game) {
      console.log('finishing game: ', game)
      axios
        .post('/api/v1/games/' + game.id + '/finish')
        .then(() => {
          this.teamsWS.send('finished game')
          this.$router.replace({ name: game, params: { gameID: game.id } })
        })
        .catch(err => {
          console.error('Could not finish game: ', game, err.response)
          this.errors = 'Could not finish game: ' + err.response.data
          this.hasErrors = true
        })
    },
    connectTeamsWS() {
      this.teamsWS = new WebSocket(this.wsUrl + '/ws/pbe/teams')
      this.teamsWS.onopen = () => {
        console.log('Connected to teams websocket')
        this.teamsWS.send('connected')
      }

      this.teamsWS.onmessage = e => {
        console.log('Received a team websocket update')
        this.games = JSON.parse(e.data)
      }
    },
    checkConnections() {
      console.log('Checking connections')
      if (this.teamsWS.readyState === this.teamsWS.CLOSED) {
        this.connectTeamsWS()
      }
    },
    openTeam(game, team) {
      this.$router.push({
        name: 'team',
        params: { gameID: game.id, teamID: team.id }
      })
    },
    deleteGame(game) {
      if (
        !confirm('Are you sure you want to delete game: ' + game.name + '?')
      ) {
        return
      }

      axios
        .delete('/api/v1/games/' + game.id)
        .then(() => {
          this.getGames()
          this.teamsWS.send('deleted game')
        })
        .catch(err => {
          console.error('Could not delete game: ' + game.id, err.response)
          this.errors =
            'Could not delete game: ' + game.name + ' : ' + err.response.data
          this.hasErrors = true
        })
    }
  }
}
</script>

<style lang="stylus" scoped>
.games
  display grid
  grid-row-gap 1em
  margin-top 10px

  .v-card__text
    padding 0

    p.title
      padding-left 10px
      margin 10px 0 5px 0

    div.teams-div
      margin 0
      padding 0

.team-join-action
  justify-content flex-end

@media (min-width: 900px)
  .games
    grid-template-columns repeat(3, 1fr)
    grid-column-gap 1em

</style>
