<template>
  <div class="main">
    <h1 class="display-2">Questions</h1>
    <v-btn
      large
      color="primary"
      @click="openAddQuestionDialog"
    ><v-icon>add</v-icon>&nbsp;New Question</v-btn>
    <div class="questions">
      <v-card v-for="question in questions" :key="question.id">
        <v-card-title primary-title class="headline secondary">
          <span style="color: #ffffff">{{ question.question }}</span>
        </v-card-title>
        <v-card-text>
          <v-list>
            <v-list-tile>
              <v-list-tile-content>
                <v-list-tile-title>{{ question.book }}</v-list-tile-title>
                <v-list-tile-sub-title>Book</v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-content>
                <v-list-tile-title>{{ question.chapter }}</v-list-tile-title>
                <v-list-tile-sub-title>Chapter</v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-content>
                <v-list-tile-title>{{ question.verses }}</v-list-tile-title>
                <v-list-tile-sub-title>Verses</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
          <v-list>
            <v-list-tile v-for="answer in question.answers" :key="answer.id" avatar>
              <v-list-tile-avatar>
                <v-icon v-if="answer.status" color="success">check_circle</v-icon>
                <v-icon v-else color="error">block</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ answer.answer }}</v-list-tile-title>
              </v-list-tile-content>
              <v-list-tile-action>
                <v-btn
                  icon
                  ripple
                  color="error"
                  @click="deleteAnswer(question, answer)"
                ><v-icon>delete</v-icon></v-btn>
              </v-list-tile-action>
            </v-list-tile>
        </v-list></v-card-text>
        <v-card-actions>
          <v-btn
            color="primary"
            large
            block
            @click="openAddAnswerDialog(question)"
          ><v-icon>add</v-icon>&nbsp;Answer</v-btn>
          <v-btn
            color="error"
            large
            block
            @click="deleteQuestion(question)"
          ><v-icon>delete</v-icon>&nbsp;Delete</v-btn>
        </v-card-actions>
      </v-card>
    </div>

    <v-dialog
      v-model="addingQuestion"
      width="400"
    >
      <v-card>
        <v-card-title
          class="headline primary"
          primary-title

        >
          <span style="color: #ffffff">Add Question</span>
        </v-card-title>

        <v-card-text>
          <v-textarea
            v-model="question.question"
            label="Question"
            required
          />
          <v-text-field
            v-model="question.book"
            label="Book"
            required
          />
          <v-text-field
            v-model="question.chapter"
            label="Chapter"
            required
          />
          <v-text-field
            v-model="question.verses"
            label="Verses"
            required
          />
        </v-card-text>

        <v-divider/>

        <v-card-actions>
          <v-spacer/>
          <v-btn
            color="secondary"
            @click="addQuestion"
          >
            <v-icon>add</v-icon> Add
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="addingAnswer"
      width="400"
    >
      <v-card>
        <v-card-title
          class="headline primary"
          primary-title

        >
          <span style="color: #ffffff">Add Answer</span>
        </v-card-title>

        <v-card-text>
          <v-textarea
            v-model="answer.answer"
            label="Answer"
            required
          />
          <v-switch
            v-model="answer.status"
            label="Correct answer?"
            color="primary"
          />
        </v-card-text>

        <v-divider/>

        <v-card-actions>
          <v-spacer/>
          <v-btn
            color="secondary"
            @click="addAnswer"
          >
            <v-icon>add</v-icon> Add
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-snackbar
      v-model="hasErrors"
      :timeout="5000"
      color="error"
    >
      {{ errors }}
      <v-btn
        dark
        flat
        @click="hasErrors = false"
      >
        Close
      </v-btn>
    </v-snackbar>

  </div>
</template>

<script>
import axios from '@/axios'
export default {
  name: 'Questions',
  data() {
    return {
      addingQuestion: false,
      question: {
        question: null
      },
      addingAnswer: false,
      answer: {
        answer: null,
        status: false
      },
      hasErrors: false,
      errors: null,
      questions: []
    }
  },
  created() {
    this.getQuestions()
  },
  methods: {
    openAddQuestionDialog() {
      this.question = {
        question: null
      }
      this.addingQuestion = true
    },
    addQuestion() {
      this.addingQuestion = true
      axios
        .post('/api/v1/questions', this.question)
        .then(() => {
          this.getQuestions()
          this.addingQuestion = false
        })
        .catch(err => {
          console.error('Could not add question: ', err.response)
          this.errors = 'Could not add question: ' + err.response.data
          this.hasErrors = true
        })
    },
    getQuestions() {
      axios
        .get('/api/v1/questions')
        .then(resp => {
          this.questions = resp.data
        })
        .catch(err => {
          console.error('Could not get questions: ', err.response)
          this.errors = 'Could not get questions: ' + err.response.data
          this.hasErrors = true
        })
    },
    getQuestion(questionID) {
      axios
        .get('/api/v1/questions/' + questionID)
        .then(resp => {
          this.question = resp.data
          this.questions.forEach((question, pos) => {
            if (question.id === questionID) {
              this.questions[pos] = this.question
            }
          })
        })
        .catch(err => {
          console.error('Could not get question: ' + questionID, err.response)
          this.errors =
            'Could not get question: ' + questionID + ' : ' + err.response.data
          this.hasErrors = true
        })
    },
    openAddAnswerDialog(question) {
      this.question = question
      this.answer = {
        answer: null,
        status: false
      }
      this.addingAnswer = true
    },
    addAnswer() {
      axios
        .post('/api/v1/questions/' + this.question.id + '/answers', this.answer)
        .then(() => {
          this.getQuestion(this.question.id)
          this.addingAnswer = false
        })
        .catch(err => {
          console.error('Could not add answer: ', err.response)
          this.errors = 'Could not add answer: ' + err.response.data
          this.hasErrors = true
        })
    },
    deleteQuestion(question) {
      axios
        .delete('/api/v1/questions/' + question.id)
        .then(() => {
          this.getQuestions()
        })
        .catch(err => {
          console.error('Could not delete question: ', err.response)
          this.errors = 'Could not delete question: ' + err.response.data
          this.hasErrors = true
        })
    },
    deleteAnswer(question, answer) {
      axios
        .delete('/api/v1/questions/' + question.id + '/answers/' + answer.id)
        .then(() => {
          this.getQuestion(question.id)
        })
        .catch(err => {
          console.error('Could not delete answer: ', err.response)
          this.errors = 'Could not delete answer: ' + err.response.data
          this.hasErrors = true
        })
    }
  }
}
</script>

<style lang="stylus" scoped>
.questions
  display grid
  grid-row-gap 1em
  margin-top 10px

  .v-card__text
    padding 0

    p.title
      padding-left 10px
      margin 10px 0 5px 0

    div.teams-div
      margin 0
      padding 0

@media (min-width: 900px)
  .questions
    grid-template-columns repeat(3, 1fr)
    grid-column-gap 1em
</style>
