<template>
  <div class="main">
    <h1 class="display-2">Finished Game</h1>
    <v-card>
      <v-card-title primary-title class="headline secondary">
        <span style="color: #ffffff">{{ game.name }}</span>
      </v-card-title>
      <v-card-text>
        <v-list>
          <v-list-tile>
            <v-list-tile-content>
              <v-list-tile-title>{{ game.seconds }}</v-list-tile-title>
              <v-list-tile-sub-title>Seconds</v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-tile>
            <v-list-tile-content>
              <v-list-tile-title>{{ game.created | date }}</v-list-tile-title>
              <v-list-tile-sub-title>Created</v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-tile>
            <v-list-tile-content>
              <v-list-tile-title>{{ game.questions }}</v-list-tile-title>
              <v-list-tile-sub-title>Questions</v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
        <p class="title">Teams</p>
        <v-list class="teams-div">
          <v-list-tile v-for="team in game.teams" :key="team.id">
            <v-list-tile-title>{{ team.name }}</v-list-tile-title>
            <v-list-tile-action class="team-join-action">
              {{ team.points }}
            </v-list-tile-action>
          </v-list-tile>
        </v-list>
      </v-card-text>
    </v-card>
    <v-snackbar
      v-model="hasErrors"
      :timeout="5000"
      color="error"
    >
      {{ errors }}
      <v-btn
        dark
        flat
        @click="hasErrors = false"
      >
        Close
      </v-btn>
    </v-snackbar>

  </div>
</template>

<script>
import axios from '@/axios'
import format from 'date-fns/format'
export default {
  name: 'Finished',
  filters: {
    date(val) {
      return format(val, 'dddd, MMM Do, YYYY h:mm:ss a')
    }
  },
  data() {
    return {
      gameID: this.$route.params.gameID,
      game: {
        name: null,
        teams: []
      },
      errors: null,
      hasErrors: false
    }
  },
  created() {
    this.getGame()
  },
  methods: {
    getGame() {
      axios
        .get('/api/v1/games/' + this.gameID + '/finished')
        .then(resp => {
          this.game = resp.data
        })
        .catch(err => {
          console.error('Could not get game: ' + this.gameID, err.response)
          this.errors =
            'Could not get game: ' + this.gameID + ' : ' + err.response.data
          this.hasErrors = true
        })
    }
  }
}
</script>

<style lang="stylus" scoped>
.team-join-action
  justify-content flex-end

</style>
