<template>
  <v-toolbar
    id="nav-toolbar"
    fixed
    app>
    <div id="nav-container">
      <v-container fluid>
        <v-layout row align-center>
          <v-flex xs11 md4>
            <v-toolbar-title id="nav-title">
              <router-link id="nav-title-link" :to="{ name: 'home' }">
                Keene Rocketeers
              </router-link>
            </v-toolbar-title>
          </v-flex>
          <v-flex v-if="mobile" xs2>
            <v-menu
              :nudge-bottom="40"
              bottom
              right
            >
              <v-btn slot="activator" icon>
                <v-icon>menu</v-icon>
              </v-btn>
              <v-list>
                <v-list-tile>
                  <v-list-tile-title @click="$router.push({name: 'home'})">PBE</v-list-tile-title>
                </v-list-tile>
                <v-list-tile>
                  <v-list-tile-title @click="$router.push({name: 'questions'})">Questions</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </v-flex>
          <v-flex v-if="!mobile" md8>
            <v-toolbar-items
              id="menu-items"
            >
              <v-layout row justify-end>
                <v-btn
                  :to="{ name: 'home' }"
                  class="menu-item"
                  flat exact>PBE</v-btn>
                <v-btn
                  :to="{ name: 'questions' }"
                  class="menu-item"
                  flat exact>Questions</v-btn>
              </v-layout>
            </v-toolbar-items>
          </v-flex>
        </v-layout>
      </v-container>
    </div>
  </v-toolbar>
</template>

<script>
export default {
  name: 'Navigation',
  data() {
    return {
      windowWidth: 0,
      mobile: false
    }
  },
  watch: {
    windowWidth(val) {
      if (val <= 500) this.mobile = true
      else this.mobile = false
    }
  },
  mounted() {
    this.windowWidth = window.innerWidth
    this.$nextTick(() => {
      window.addEventListener('resize', () => {
        this.windowWidth = window.innerWidth
      })
    })
  }
}
</script>

